<template>
  <div class="about">
    <h1> </h1>

    {{ paging }}
    <hr>

    {{ responseData }}

    <h4> {{ paging.pageNum }} </h4>

    <!--  -->
    <button 
      @click="paging.pageNum = paging.startPage + 1"
      v-if="paging.prev" 
      class="btn btn-primary"></button>

    <!--   -->
    <span v-for="number in paging.endPage" :key="number">
      <button 
        @click="getAlbum(number)"
        class="btn btn-primary"
        v-if="paging.startPage == 1 || number-1 > paging.startPage">{{ number }}</button>
      
    </span>


    <!-- -->
    <button 
      @click="paging.pageNum = paging.endPage + 1"
      v-if="paging.next" 
      class="btn btn-primary"></button>
    


  </div>
</template>



<script setup>
import { ref } from 'vue';
import { usePagingStore } from '@/stores/paging.js'
import { useGetDataStore } from '@/stores/GetData.js'
const paging = usePagingStore();
const getData = useGetDataStore();



let responseData = ref('');
const getAlbum = async (number) => {
  paging.pageNum = number;
  responseData.value = await getData.getAlbumData(paging.pageNum);
  console.log( responseData.value );
};

</script>